@media (max-width: 720px) {
  .hero {
    position: relative;
    &::before {
      content: "";
      position: absolute;
      left: 0;
      top: 0;
      right: 0;
      bottom: 0;
      background-color: rgba(#000, 0.5);
    }
    .header {
      &__logo {
        z-index: 1;
      }
      &__phone {
        z-index: 1;
      }
      &__call {
        z-index: 1;
      }
    }
  }
  .nav {
    display: block;
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;
    padding: 0;
    li {
      margin-bottom: 20px;
      font-size: 25px;
    }
    z-index: 1;
  }
  .wrapper {
    .section {
      &__right {
        width: auto;
        margin: 10px 0;
        float: none;
      }
      &__left {
        width: auto;
        float: none;
      }
    }
  }
}

@media (max-width: 960px) {
  .wrapper {
    .best {
      &__motto {
        width: auto;
        float: none;
        font-size: 40px;
        .br {
          display: none;
        }
      }
      &__text {
        width: auto;
        float: none;
      }
    }
    .testis {
      display: block;
    }
    .person {
      margin-bottom: 30px;
    }
  }
}

@media (max-width: 600px) {
  .wrapper {
    .section {
      &__title {
        top: -5px;
        font-size: 30px;
        display: block;
        width: 100%;
        text-align: center;
      }
    }
  }
}
